import React from 'react';
import Button from '@mui/material/Button';



interface ButtonProps {
  onClick: () => void;
  disabled?: boolean;
  className?: string;
  fgColor?:"primary" | "secondary" | undefined;
  children: React.ReactNode;
}

const MiniteButton: React.FC<ButtonProps> = ({ onClick, disabled = false, className = '',fgColor, children }) => {
  return (
   
    <Button variant="contained" color={fgColor}> {children} onClick={onClick}</Button>
   
  );
};

export default MiniteButton;
